import React, { useCallback, useEffect } from "react";
import Button from "@mui/material/Button";
import { ToggleButton, ToggleButtonGroup } from "@mui/material";
import DietForm from "./DietForm";
import Chart from "./Chart";
import { logOut, retrieveUser } from "../services/auth";
import { retrieveDiets } from "../services/diets";

const MyQS = () => {
  const { user } = retrieveUser();
  const { diets } = retrieveDiets();

  const [periodDiet, setPeriodDiet] = React.useState();
  const [periodDietButton, setPeriodDietButton] = React.useState(() => [
    "week",
  ]);

  const getData = useCallback(
    (period) => {
      const savedDietFilteredByPeriod = diets.filter((diet) => {
        const dietDate = new Date(diet.date);
        const today = new Date();
        if (period === "week") {
          const rawToday = new Date();
          const pastWeek = rawToday.setDate(rawToday.getDate() - 7);
          return dietDate > pastWeek;
        }
        if (period === "month") {
          return dietDate > today.setMonth(today.getMonth() - 1);
        }
        if (period === "year") {
          return dietDate > today.setFullYear(today.getFullYear() - 1);
        }
        return true;
      });
      const veganDiet = savedDietFilteredByPeriod.filter(
        (diet) => diet.diet === "vegan"
      );
      const vegetarianDiet = savedDietFilteredByPeriod.filter(
        (diet) => diet.diet === "vegetarian"
      );
      const omnivoreDiet = savedDietFilteredByPeriod.filter(
        (diet) => diet.diet === "omnivore"
      );

      const periodDietArr = [];

      if (veganDiet.length > 0) {
        periodDietArr.push({
          title: "vegan",
          value: veganDiet.length,
          color: "#75DBCD",
        });
      }
      if (vegetarianDiet.length > 0) {
        periodDietArr.push({
          title: "v√©g√©tarien",
          value: vegetarianDiet.length,
          color: "#FAA381",
        });
      }
      if (omnivoreDiet.length > 0) {
        periodDietArr.push({
          title: "omnivore",
          value: omnivoreDiet.length,
          color: "#DCDBA8",
        });
      }

      setPeriodDiet(periodDietArr);

      return undefined;
    },
    [diets]
  );

  const handleChange = (event, periodDietSelected) => {
    getData(periodDietSelected);
    setPeriodDietButton(periodDietSelected);
  };

  useEffect(() => {
    getData("week");
  }, [diets, getData]);

  const getDietNumber = () => {
    const dietDates = diets.reduce((previousValue, currentValue) => {
      previousValue.push(currentValue.date);
      return previousValue;
    }, []);

    const dietDatesWithoutDuplicateDates = dietDates.filter(
      (item, index) => dietDates.indexOf(item) === index
    );

    return dietDatesWithoutDuplicateDates.length;
  };

  const getDatesFromSavedDiet = () =>
    diets.reduce((previousValue, currentValue) => {
      previousValue.push(currentValue.date);
      return previousValue;
    }, []);

  const getNearestDate = () => {
    const today = new Date();
    const dates = getDatesFromSavedDiet();
    const sortedByDiff = [...dates].sort(
      (a, b) => Math.abs(new Date(a) - today) - Math.abs(new Date(b) - today)
    );

    return sortedByDiff[0];
  };

  const nearestDate = getNearestDate()
    ? new Date(getNearestDate()).toLocaleDateString("fr")
    : "Aucune ü§∑‚Äç";

  return (
    <>
      <ToggleButtonGroup
        value={periodDietButton}
        color="primary"
        className="periodButton"
        exclusive
        onChange={handleChange}
        size="small"
      >
        <ToggleButton value="week">Semaine</ToggleButton>
        <ToggleButton value="month">Mois</ToggleButton>
        <ToggleButton value="year">Ann√©e</ToggleButton>
        <ToggleButton value="all">Tout</ToggleButton>
      </ToggleButtonGroup>

      <Chart
        diets={diets}
        data={periodDiet}
        label={({ dataEntry }) =>
          dataEntry.value > 0
            ? `${dataEntry.title} : ${Math.round(dataEntry.percentage)}%`
            : null
        }
      />

      <aside className="nearestDate">
        <p>Date la plus proche renseign√©e : {nearestDate}</p>
      </aside>

      <DietForm />

      <div>
        <p>Nombre de jours : {getDietNumber()}</p>
      </div>

      <hr />
      {user && (
        <div>
          <p>Utilisateur: {user.email}</p>
          <Button
            onClick={logOut}
            className="logOut"
            type="submit"
            variant="outlined"
          >
            Se d√©connecter
          </Button>
        </div>
      )}

      <hr />
    </>
  );
};

MyQS.propTypes = {};

export default MyQS;
