import React from "react";
import PropTypes from "prop-types";
import { PieChart } from "react-minimal-pie-chart";

function shouldShowMessage(diets, data) {
  return diets.length === 0 || data.length === 0;
}

function showMessage(diets) {
  return (
    <div className="chartEmpty">
      <p>
        Remplis le formulaire{" "}
        {diets.length === 0 ? "" : "pour la p√©riode s√©lectionn√©e"} üòâ
      </p>
    </div>
  );
}

const Chart = ({ diets, data, label }) => (
  <div className="chartContainer">
    {shouldShowMessage(diets, data) ? (
      showMessage(diets)
    ) : (
      <PieChart data={data} label={label} labelStyle={{ fontSize: "6px" }} />
    )}
  </div>
);

Chart.propTypes = {
  diets: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      value: PropTypes.number,
      color: PropTypes.string,
    })
  ).isRequired,
  data: PropTypes.arrayOf(
    PropTypes.shape({
      date: PropTypes.string,
      diet: PropTypes.string,
      meal: PropTypes.string,
    })
  ).isRequired,
  label: PropTypes.func.isRequired,
};

export default Chart;
